import React from 'react'
import { Text } from 'uikit'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import useTokenBalance from 'hooks/useTokenBalance'
import BigNumber from 'bignumber.js'
import { getTokenAddress } from 'utils/addressHelpers'
import { getBalanceNumber } from 'utils/formatBalance'

interface CardValueProps {
  aPrice?: BigNumber
}

const CakeWalletBalance: React.FC<CardValueProps> = ({ aPrice }) => {
  const { chainId, account } = useWallet()
  const cakeBalance = useTokenBalance(getTokenAddress(chainId))

  if (!account) {
    return (
      <Text color="textDisabled" style={{ lineHeight: '36px' }}>
        Locked
      </Text>
    )
  }
  const fixedget = getBalanceNumber(cakeBalance, 18).toFixed(4)
  const allEarningsX = aPrice.multipliedBy(getBalanceNumber(cakeBalance, 18)).toFixed(2)

  return (
    <Text fontSize="24px" style={{ lineHeight: '60px' }}>
      {' '}
      {` ${fixedget} ( ${allEarningsX} $ )`}{' '}
    </Text>
  )
}

export default CakeWalletBalance
